/**
 * 格式化秒
 *
 * @param result
 * @returns {string}
 */
export function formatSecond(result: number) {
    let formatText = '';
    const h = Math.floor(result / 3600);
    const m = Math.floor((result / 60) % 60);
    const s = Math.floor(result % 60);
    formatText = `${s}秒`;
    if (m > 0) {
        formatText = `${m}分钟${formatText}`;
    }
    if (h > 0) {
        formatText = `${h}小时${formatText}`;
    }
    return formatText;
}
