







import { Component, Mixins, Prop, Watch } from "vue-property-decorator";
import TwoWay from "@/resources/mixins/two-way";
import { formatSecond } from "./util";

@Component({
    name: "MySecond"
})
export default class MySecond extends Mixins(TwoWay) {
    @Prop({ default: "edit" })
    mode!: string;

    get initValue() {
        return this.currentValue ? formatSecond(this.currentValue) : "-";
    }
}
